<template>
  <div>
    <div class="grid-x">
      <div class="medium-3"></div>
      <div class="medium-6">
        <h3>{{gallery.name}}</h3>
        <h4 class="subhea">{{gallery.des}}</h4>
        <div>
          <img
            v-for="image in gallery.photos"
            :key="image"
            :src="'http://106.15.183.147:3000/user/avatars/'+image"
            alt
            style="margin-bottom:2rem"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Zooming from "zooming";
export default {
  data() {
    return {
      gallery: {}
    };
  },
  created() {
    this.axios.get("gallery/edit/" + this.$route.params.id).then(res => {
      console.log(res.data);
      this.gallery = res.data;
    });
  },
  mounted() {
    setTimeout(() => {
      new Zooming().listen("img");
    }, 500);
  }
};
</script>

