<template>
  <div class="grid-x">
    <div class="medium-3"></div>
    <div class="medium-6">
      <div v-for="g in galleries" :key="g.id">
        <router-link :to="'/gabyid/'+g.id">
          <h3>{{ g.name }}</h3>
        </router-link>
        <h4 class="subheader">{{g.des}}</h4>
        <div class>
          <img
            :src="'http://106.15.183.147:3000/user/avatars/'+img"
            alt
            v-for="img in g.photos"
            :key="img"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      galleries: ""
    };
  },
  created() {
    this.axios.get("gallery/" + 1).then(res => {
      this.galleries = res.data;
    });
  }
};
</script>

<style scoped>
img {
  height: auto;
  width: 50%;
}
</style>